import React from "react";
import { Card, Statistic } from "antd";
import { NodeIndexOutlined } from "@ant-design/icons/es/icons";

export default function TotalSessions({ totalSessions }) {


  return (
    <Card bordered={false} >
      <Statistic
        title="Total Sessions"
        value={totalSessions}
        valueStyle={{ color: "#1890ff" }}
        suffix="Sessions"
        prefix={<NodeIndexOutlined />}
      />
    </Card>
  );
}
